<template>
  <div>
    <Header name="购买成功"></Header>
    <div class="container">
      <div class="img">
        <img :src="require('@/assets/image/paySuccess.png')" alt="" />
      </div>
      <div class="font">
        <p class="no1">商品购买成功~</p>
        <p class="no2">订单编号：{{orderNum}}</p>
      </div>
      <div class="btn" @click="goHome">返回首页</div>
    </div>
  </div>
</template>

<script>

import Header from '@/components/Layout/header/index'

export default {
  components :{
    Header
  },
  data(){
    return{
      orderNum: this.$route.query.orderNum
    }
  },
  methods:{
    goHome(){
      this.$router.push({
        path: '/'
      })
    }
  }
};
</script>

<style lang="stylus" scoped>
.container {
  margin-top: 142px
  display: flex;
  flex-direction: column;
  align-items: center;

  .img {
    width: 70px;
    height: 72px;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .font {
    text-align: center;
    margin-top: 23px
    .no1 {
      font-size: 18px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #111111;
    }

    .no2 {
      margin-top: 10px
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #777777;
    }
  }

  .btn {
    margin-top: 42px
    width: 110px;
    height: 35px;
    background: #FF4C4C;
    border-radius: 2px;
    color: #fff;
    text-align: center;
    line-height: 35px;
  }
}
</style>


